.footer__copyright {
    font-family: Inter,Arial, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #545454;
    margin: 0 0 0 19px;
    padding: 0;
}

@media (min-width: 768px) {
    .footer__copyright {
        margin: 0 0 0 27px;
    }
}

@media (min-width: 1024px) {
    .footer__copyright {
        margin: 0;
        font-size: 18px;
        line-height: 22px;
    }
}